@keyframes flickerAnimation {
  0% {
    opacity: 0.35;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0.35;
  }
}
@-o-keyframes flickerAnimation {
  0% {
    opacity: 0.35;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0.35;
  }
}
@-moz-keyframes flickerAnimation {
  0% {
    opacity: 0.35;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0.35;
  }
}
@-webkit-keyframes flickerAnimation {
  0% {
    opacity: 0.35;
