/*============================ 
// Global styles
=============================*/
.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

section,
.section {
  position: relative;
  overflow: hidden;
}

@media (min-width: 1400px) {
  .container {
    max-width: 1194px;
  }
  .container-lg {
    max-width: 1584px;
  }
}
@keyframes toBottomFromTop {
  49% {
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
/*===========================
//	Utility CSS.
===========================*/
.section-padding {
  padding: 120px 0;
}
@media (max-width: 991px) {
