// para-text/normal text style
=================================*/
.circle-radius {
  border-radius: 50%;
}

.sticky-tab-style {
  position: sticky;
  top: 77px;
  z-index: 1;
  background: white;
  padding: 15px 25px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 0px 0px 8px 8px;
  transition: all 0.4s ease-in-out;
}

.animate__animated {
  opacity: 0;
  visibility: hidden;
}

.swiper-vertical {
  max-height: 205px;
}

.marquee {
  width: 100%;
  overflow: hidden;
}

/** Bootstrap Row customize**/
.nice-select {
  border: none;
}
/*=============================
// Bootstrap Row gutter reset
==============================*/
@media (min-width: 1200px) {
  .row.gutters-30 {
    margin-left: -15px;
    margin-right: -15px;
  }
  .row.gutters-30 > [class^=col-] {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media only screen and (min-width: md min) {
  .swiper {
    padding-bottom: 120px;
  }
}

.swiper-pagination .swiper-pagination-bullet {
  background: #D9D9D9;
  width: 10px;
  height: 10px;
  opacity: 1;
}

.swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #12D28C;
}

.header-hero-wrapper {
  background: linear-gradient(92.92deg, #2405F3 9.1%, #5C1CE2 99.72%);
  position: relative;
}
.header-hero-wrapper::before {
  content: "";
  background-image: url("../images/hero-section/opacity-bg.png");
  position: absolute;
  background-size: cover;
  background-position: center;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
}

/* ===========================
// Typography
==============================*/
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 1rem;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  margin: 0;
  padding: 0;
  color: var(--color-title);
}

h1 {
  font-size: 40px;
}
@media only screen and (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 36px;
  line-height: normal;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 22px;
  line-height: normal;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

p {
  color: var(--color-text);
  font-size: 1rem;
  font-weight: 400;
  line-height: 26px;
  padding: 0;
  margin: 0;
}

ol {
  padding-left: 1rem;
}
ol li {
  padding: 0;
  margin: 0;
  list-style: decimal;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  display: inline-block;
  transition: all 0.4s ease-in-out;
  text-decoration: none;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
