  width: 1px;
  height: 100%;
  left: 0;
  top: 0;
  transform: translate3d(0px, 102%, 0px);
}
.e-button .line--two {
  width: 100%;
  height: 1px;
  left: 0;
  top: 0;
  transform: translate3d(-102%, 0px, 0px);
}
.e-button .line--three {
  width: 1px;
  height: 100%;
  right: 0;
  top: 0;
  transform: translate3d(0px, -102%, 0px);
}
.e-button .line--four {
