  width: 60%;
  color: #a1a1a1;
  font-size: 20px;
  text-align: left;
  border: none;
  margin: 0 auto;
  padding: 10px 0;
  outline: none;
  background-color: transparent;
  border-bottom: 1px solid #d9d9d9;
}

.template-search .search-form input[type=search]:focus {
  border-color: #d9d9d9;
}

.template-search .search-form .search-btn {
  margin-right: 20px;
  -webkit-box-shadow: none;
  box-shadow: none;
  border: none;
  cursor: pointer;
  font-size: 15px;
  font-weight: 900;
  background-color: transparent;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.template-search .search-form .search-btn i {
  position: relative;
  right: 20px;
}

.template-search .search-form .search-btn:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
  outline: none;
}

