  color: #a1a1a1;
}

.template-search .search-form input::-moz-placeholder {
  color: var(--color-primary);
}

.template-search .search-form input:-moz-placeholder {
  color: var(--color-primary);
}

.template-search .search-form input:-ms-input-placeholder {
  color: var(--color-primary);
}

.template-search .close {
  position: fixed;
  top: 50px;
  right: 100px;
  color: var(--color-black);
  border: none;
  visibility: visible;
  font-size: 30px;
  font-weight: 600;
